import { useState, useCallback, useEffect, useRef, useMemo } from 'react';
import { useCedrosLogin } from '../context/useCedrosLogin';
import { ApiClient, handleApiError } from '../utils/apiClient';
import type { AuthResponse, AuthError } from '../types';

/**
 * Module-level singleton for Google script loading (P-01)
 *
 * Prevents race conditions when multiple components mount simultaneously.
 * Uses a promise queue pattern to ensure the script is only loaded once.
 *
 * ## SSR Limitations (F-08)
 *
 * This singleton persists across the module lifecycle. In SSR environments:
 * - Module state persists between requests (potential cross-request leakage)
 * - Google Sign-In requires browser APIs (document, window)
 * - The hook guards against SSR with `googleClientId` checks
 *
 * For SSR frameworks (Next.js, Remix), ensure this hook is only used
 * in client-side components.
 *
 * ## Test Isolation
 *
 * For test isolation, call `scriptLoader._reset()` in test setup/teardown.
 *
 * @internal
 */
const scriptLoader = {
  loading: false,
  loaded: false,
  error: null as Error | null,
  callbacks: [] as Array<{ resolve: () => void; reject: (err: Error) => void }>,

  load(): Promise<void> {
    // SSR guard: avoid touching browser globals when running in Node/SSR.
    if (typeof window === 'undefined' || typeof document === 'undefined') {
      return Promise.reject(new Error('Google Sign-In script loader cannot run in SSR'));
    }

    // Already loaded
    if (this.loaded) {
      return Promise.resolve();
    }

    // Loading in progress - queue callback
    if (this.loading) {
      return new Promise((resolve, reject) => {
        this.callbacks.push({ resolve, reject });
      });
    }

    // Start loading
    this.loading = true;
    return new Promise((resolve, reject) => {
      this.callbacks.push({ resolve, reject });

      // Check if script already exists (from previous session or SSR)
      const existingScript = document.getElementById('google-gsi-script');
      if (existingScript) {
        if (window.google?.accounts?.id) {
          this.loaded = true;
          this.loading = false;
          this.callbacks.forEach((cb) => cb.resolve());
          this.callbacks = [];
        } else {
          existingScript.addEventListener('load', () => {
            this.loaded = true;
            this.loading = false;
            this.callbacks.forEach((cb) => cb.resolve());
            this.callbacks = [];
          });
        }
        return;
      }

      const script = document.createElement('script');
      script.src = 'https://accounts.google.com/gsi/client';
      script.async = true;
      script.defer = true;
      script.id = 'google-gsi-script';

      script.onload = () => {
        this.loaded = true;
        this.loading = false;
        this.callbacks.forEach((cb) => cb.resolve());
        this.callbacks = [];
      };

      script.onerror = () => {
        this.loading = false;
        // M-02: Remove failed script from DOM to allow retry on next load() call.
        // Without removal, retry finds existingScript and waits for a load event
        // that will never fire on an already-failed script.
        script.remove();
        const error = new Error('Failed to load Google Sign-In script');
        this.callbacks.forEach((cb) => cb.reject(error));
        this.callbacks = [];
      };

      document.head.appendChild(script);
    });
  },

  /**
   * Reset singleton state for test isolation (F-08)
   * @internal - Only use in test setup/teardown
   */
  _reset(): void {
    this.loading = false;
    this.loaded = false;
    this.error = null;
    this.callbacks = [];
  },
};

/** @internal */
export const _internalGoogleScriptLoader = scriptLoader;

export interface UseGoogleAuthReturn {
  signIn: () => Promise<AuthResponse>;
  isLoading: boolean;
  isInitialized: boolean;
  error: AuthError | null;
  clearError: () => void;
}

interface PromiseCallbacks {
  resolve: (value: AuthResponse) => void;
  reject: (error: AuthError) => void;
}

/**
 * Hook for Google OAuth authentication.
 *
 * @example
 * ```tsx
 * function GoogleButton() {
 *   const { signIn, isLoading, isInitialized, error } = useGoogleAuth();
 *
 *   return (
 *     <button onClick={signIn} disabled={!isInitialized || isLoading}>
 *       {isLoading ? 'Signing in...' : 'Sign in with Google'}
 *     </button>
 *   );
 * }
 * ```
 */
export function useGoogleAuth(): UseGoogleAuthReturn {
  const { config, _internal } = useCedrosLogin();
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialized, setIsInitialized] = useState(false);
  const [error, setError] = useState<AuthError | null>(null);

  const promiseCallbacksRef = useRef<PromiseCallbacks | null>(null);
  const configRef = useRef(config);

  const apiClient = useMemo(
    () =>
      new ApiClient({
        baseUrl: config.serverUrl,
        timeoutMs: config.requestTimeout,
        retryAttempts: config.retryAttempts,
      }),
    [config.serverUrl, config.requestTimeout, config.retryAttempts]
  );

  // Keep config ref in sync
  useEffect(() => {
    configRef.current = config;
  }, [config]);

  // Handle credential response from Google
  const handleCredentialResponse = useCallback(
    async (response: { credential: string }) => {
      const callbacks = promiseCallbacksRef.current;
      if (!callbacks) return;

      try {
        const data = await apiClient.post<AuthResponse>('/google', {
          idToken: response.credential,
        });
        configRef.current.callbacks?.onLoginSuccess?.(data.user, 'google');
        _internal?.handleLoginSuccess(data.user, data.tokens);
        setIsLoading(false);
        callbacks.resolve(data);
      } catch (err) {
        const authError = handleApiError(err, 'Google sign-in failed');
        setError(authError);
        setIsLoading(false);
        callbacks.reject(authError);
      } finally {
        promiseCallbacksRef.current = null;
      }
    },
    [apiClient, _internal]
  );

  // P-01: Initialize Google Sign-In SDK using singleton loader
  useEffect(() => {
    // Early return if Google auth is not enabled
    if (!config.googleClientId) {
      return;
    }

    // Track mounted state to prevent state updates after unmount
    let isMounted = true;

    const initializeGoogleSignIn = () => {
      if (!isMounted) return;

      window.google?.accounts?.id?.initialize({
        client_id: config.googleClientId!,
        callback: handleCredentialResponse,
        auto_select: false,
        cancel_on_tap_outside: true,
      });

      if (isMounted) {
        setIsInitialized(true);
      }
    };

    // Use singleton loader to handle script loading
    scriptLoader
      .load()
      .then(() => {
        if (isMounted) {
          initializeGoogleSignIn();
        }
      })
      .catch(() => {
        if (isMounted) {
          setError({
            code: 'SERVER_ERROR',
            message: 'Failed to load Google Sign-In',
          });
        }
      });

    return () => {
      isMounted = false;
    };
  }, [config.googleClientId, handleCredentialResponse]);

  const signIn = useCallback(async (): Promise<AuthResponse> => {
    if (!config.googleClientId) {
      const err: AuthError = {
        code: 'VALIDATION_ERROR',
        message: 'Google Client ID not configured',
      };
      setError(err);
      throw err;
    }

    if (!isInitialized) {
      const err: AuthError = {
        code: 'VALIDATION_ERROR',
        message: 'Google Sign-In not initialized',
      };
      setError(err);
      throw err;
    }

    if (promiseCallbacksRef.current) {
      const err: AuthError = {
        code: 'VALIDATION_ERROR',
        message: 'Google Sign-In already in progress',
      };
      setError(err);
      throw err;
    }

    setIsLoading(true);
    setError(null);

    return new Promise<AuthResponse>((resolve, reject) => {
      promiseCallbacksRef.current = { resolve, reject };

      // Show Google One Tap prompt
      window.google?.accounts?.id?.prompt((notification) => {
        if (notification.isNotDisplayed()) {
          const err: AuthError = {
            code: 'SERVER_ERROR',
            message: 'Google Sign-In popup was blocked. Please allow popups or try again.',
          };
          setError(err);
          setIsLoading(false);
          promiseCallbacksRef.current = null;
          reject(err);
        } else if (notification.isSkippedMoment()) {
          const err: AuthError = {
            code: 'SERVER_ERROR',
            message: 'Google Sign-In was cancelled',
          };
          setError(err);
          setIsLoading(false);
          promiseCallbacksRef.current = null;
          reject(err);
        } else if (notification.isDismissedMoment()) {
          const err: AuthError = {
            code: 'SERVER_ERROR',
            message: 'Google Sign-In was cancelled',
          };
          setError(err);
          setIsLoading(false);
          promiseCallbacksRef.current = null;
          reject(err);
        }
      });
    });
  }, [config.googleClientId, isInitialized]);

  const clearError = useCallback(() => setError(null), []);

  return {
    signIn,
    isLoading,
    isInitialized,
    error,
    clearError,
  };
}

// Type declaration for Google Identity Services
declare global {
  interface Window {
    google?: {
      accounts?: {
        id?: {
          initialize: (config: {
            client_id: string;
            callback: (response: { credential: string }) => void;
            auto_select?: boolean;
            cancel_on_tap_outside?: boolean;
          }) => void;
          prompt: (
            callback: (notification: {
              isNotDisplayed: () => boolean;
              isSkippedMoment: () => boolean;
              isDismissedMoment: () => boolean;
              getMomentType: () => string;
            }) => void
          ) => void;
          renderButton: (element: HTMLElement, config: object) => void;
          disableAutoSelect: () => void;
        };
      };
    };
  }
}
